
$customFont: 'Barlow Condensed';


$font-family-sans-serif:      $customFont, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol" !default;
$font-family-monospace:       'PT Serif', 'SFMono-Regular', Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace !default;
$font-family-base:            $font-family-monospace;


$primaryColor: hsl(6, 92, 54);
$primaryColor1: darken($primaryColor, 10%);
$primaryColor2: darken($primaryColor, 5%);
$primaryColor3: lighten($primaryColor, 10%);
$primaryColor4: lighten($primaryColor, 25%);


$wdwBlack: hsl(1, 0, 20);
$wdwBlack1: darken($wdwBlack, 15%);
$wdwBlack2: darken($wdwBlack, 5%);
$wdwBlack3: lighten($wdwBlack, 10%);
$wdwBlack4: lighten($wdwBlack, 25%);

$wdwGrey: hsl(0, 0, 85);
$wdwGrey1: darken($wdwGrey, 15%);
$wdwGrey2: darken($wdwGrey, 5%);
$wdwGrey3: lighten($wdwGrey, 5%);
$wdwGrey4: lighten($wdwGrey3, 5%);

$wdwBlue: hsl(216, 100, 39);
$wdwBlue1: darken($wdwBlue, 15%);
$wdwBlue2: darken($wdwBlue, 10%);
$wdwBlue3: lighten($wdwBlue, 5%);
$wdwBlue4: lighten($wdwBlue, 10%);

$wdwPurple: hsl(306, 65, 45);
$wdwPurple1: darken($wdwPurple, 10%);
$wdwPurple2: darken($wdwPurple, 5%);
$wdwPurple3: lighten($wdwPurple, 15%);
$wdwPurple4: lighten($wdwPurple, 20%);

$wdwOrange: hsl(32, 100, 60);
$wdwOrange1: darken($wdwOrange, 10%);
$wdwOrange2: darken($wdwOrange, 5%);
$wdwOrange3: lighten($wdwOrange, 15%);
$wdwOrange4: lighten($wdwOrange, 32%);

$wdwGreen: hsl(134, 81, 40);
$wdwGreen1: darken($wdwGreen, 10%);
$wdwGreen2: darken($wdwGreen, 5%);
$wdwGreen3: lighten($wdwGreen, 15%);
$wdwGreen4: lighten($wdwGreen, 25%);
