@supports(display: grid ) {
	.wdwcontent-center {
		display: grid;
		grid-gap: 1em;
		grid-template-columns: 1fr;
		main {
			justify-self: center;
			grid-column: 1 / 2;
			grid-row: 1 / 2; }
		.side {
			grid-column: 1 / 2;
			grid-row: 2 / 3;
			//border: 1px solid $wdwGrey1
 } } }			//background: white

@media (min-width: 55rem) {
	@supports(display: grid ) {
		.wdwcontent-center {
			width: 90vw;
			margin: 0 auto;
			display: grid;
			grid-gap: 1em;
			grid-template-columns: 1fr 1fr;
			main {
				justify-self: center;
				grid-column: 1 / 2;
				grid-row: 1 / 2; }
			.side {
				grid-column: 2 / 3;
				grid-row: 1 / 2;
				//border: 1px solid $wdwGrey1
 } } } }				//background: white

@media (min-width: 65rem) {
	@supports(display: grid ) {
		.wdwcontent-center {
			grid-template-columns: 1fr 1fr; } } }


