.side {
	&--title {} }

.smallSiteList {
	//border: 1px solid red
	@include cardRadius(0, $cardRadius, 0, $cardRadius);
	@include cardShadow($cardShadowColor);
	background: white;
	padding: 0;
	margin-bottom: 1rem;
	.side--title {
		padding: 0 1rem; }
	&--thumb {
		margin-left: .8em;
		border-radius: 50%; }
	&--list {
		padding: 0;
		margin: 0;
		list-style: none;
		display: flex;
		justify-content: space-between;
		flex-direction: column;//row | row-reverse | column | column-reverse;
 }		//align-items: center// flex-start | flex-end | center | baseline | stretch
	&--item {
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: space-between;
		border-bottom: 1px solid rgba(black, .1);
		padding: .5em 0;
		&:last-child {
			border-bottom: 1px solid transparent; } }
	&--title {
		font-family: $customFont;
		color: $wdwGrey1;
		font-size: 1.2rem;
		text-align: left;
		margin-right: auto;
		//align-self: center//auto | flex-start | flex-end | center | baseline | stretch
		> span {
			padding-left: 1rem; } }
	.cta--pn {
		margin-right: 1em; } }

