$heroAreaWidth: 110%;
.hero-area {
	position: relative;
	overflow: hidden;
	padding: 2rem 0;
 }	//margin-bottom: 1rem

.cover {
	background-color: lighten($wdwBlue4, 20%);
	//background-color: #88dbff
	background-image: url(#{$path}bg.png);
	background-position: center;
	//background-size: cover
	//-webkit-filter: url('../images/bg.png')
	//filter: url('../images/bg.png')
	//-webkit-filter: blur(3px)
	filter: blur(3px); //none | blur() | brightness() | contrast() | drop-shadow() | grayscale() | hue-rotate() | invert() | opacity() | saturate() | sepia() | url();
	width: $heroAreaWidth;
	height: $heroAreaWidth;
	position: absolute;
	left: $heroAreaWidth/2;
	top: $heroAreaWidth/2;
	//border: 1px solid red
	transform: translate(-$heroAreaWidth/2, -$heroAreaWidth/2);
	z-index: -1; }
