.siteCard[data-cardTheme="data-offerCard-exclusive"] {
	--borderColor: rgba($exclusive, .5);
	--bgColor: darken($exclusive, 10%);
	--linkColor: darken($exclusive, 35%);
	//--starsBg: white

	// button
	//--btn-bg: darken($exclusive, 35%)
	--btn-bg: linear-gradient(45deg, darken($exclusive, 33%) 25%, darken($exclusive, 30%) 25%, darken($exclusive, 30%) 50%, darken($exclusive, 33%) 50%, darken($exclusive, 33%) 75%, darken($exclusive, 30%) 75%, darken($exclusive, 30%));
	//--btn-bg-hover: darken($exclusive, 30%)
	--btn-bg-hover: linear-gradient(45deg, darken($exclusive, 31%) 25%, darken($exclusive, 25%) 25%, darken($exclusive, 25%) 50%, darken($exclusive, 31%) 50%, darken($exclusive, 31%) 75%, darken($exclusive, 25%) 75%, darken($exclusive, 25%));
	--btn-color: rgba(white, .9);
	--btn-color-hover: rgba(white, 1);
	a {
		background-size: 20px 20px;
		animation: barberpole 0.5s linear infinite;
		&:hover {
 } } }			//animation: barberpole 0.5s linear infinite

@keyframes barberpole {
	0% {
		background-position: 0 0; }
	100% {
		background-position: 50px 30px; } }

.siteCard[data-cardTheme="data-offerCard-new"] {
	--borderColor: rgba($design, .5);
	--bgColor: darken($design, 10%);
	--linkColor: darken($design, 20%);
	//--starsBg: white

	// button
	--btn-bg: darken($design, 35%);
	--btn-bg-hover: darken($design, 30%);
	--btn-color: rgba(white, .9);
	--btn-color-hover: rgba(white, 1); }

.siteCard[data-cardTheme="data-offerCard-new-design"] {
	--borderColor: rgba($new, .5);
	--bgColor: darken($new, 10%);
	--linkColor: darken($new, 20%);
	//--starsBg: white

	// button
	--btn-bg: darken($new, 35%);
	--btn-bg-hover: darken($new, 30%);
	--btn-color: rgba(black, .8);
	--btn-color-hover: rgba(white, 1); }


