.siteCard {
	background: white;
	box-shadow: 0px 15px 20px -15px var(--borderColor, $wdwOrange3);
	transition: all 0.3s ease-in-out;
	//background: var(--bgColor)
	border: var(--border, 2px) solid var(--borderColor, transparent);
	//background: linear-gradient(var(--borderColor) 20%, var(--bgColor) 100%)
	&:hover {
		//transform: scale(1.01, 1.01)
		box-shadow: 0px 20px 25px -20px var(--linkColor, $wdwOrange3); }
	&--title {
		color: $wdwBlack; } }

