.hero-area_offer {
	list-style: none; }
.offers {
	.card {
		background: white; }
	.tac {
		color: rgba($wdwBlack1, .95);
		//color: rgba(white, .8)
		font-family: $customFont;
		line-height: 1em; } }
.offers {
	.card {
		//max-width: 22em
		//margin: 0 auto
		//justify-self: stretch
		//background: white
		padding-bottom: 1rem;
		border-radius: 8px;
		//overflow: hidden
		box-shadow: 0px 10px 10px -5px rgba(blue, 0.2);
		.fig {
			border-radius: 8px 8px 0px 0px;
			$size-offers--ico: 3px;
			.offers--ico {
				text-orientation: sideways;
				position: absolute;
				left: $size-offers--ico * 4;
				top: $size-offers--ico * -2;
				background: rgba(white, 1);
				z-index: 20;
				padding: .3em .5em .3em .5em;
				border-radius: 0 0 $size-offers--ico $size-offers--ico;
				font-family: $customFont;
				font-size: 1.2em;
				text-transform: uppercase;
				color: rgba(black, .2);
				color: var(--b);
				&::after {
					content: "";
					display: block;
					width: 0px;
					height: 0px;
					//background: red
					position: absolute;
					top: $size-offers--ico;
					top: 0;
					right: $size-offers--ico * -2;
					border: $size-offers--ico solid black;
					border-right-color: transparent;
					border-top-color: transparent;
					z-index: 10; } } }


		img {
			max-height: 100%; }
		> * {
			//align-self: start
 }			//border: 1px solid red

		h2 {
			text-decoration: none;
			font-weight: normal;
			font-size: 1.2rem;
			font-family: $customFont;
			margin-top: 1rem;
			a {
				color: $wdwBlack4; }
			small {
				color: $wdwBlack4;
				font-style: italic;
				font-size: 1.2rem;
				color: $wdwBlack; } }
		.offer-txt {
			//min-height: 3rem
			font-size: 1.4rem;
			line-height: 1.2em;
			font-weight: 800;
			font-family: $customFont;
			a {
				color: $wdwBlue2; } } } }
