.siteCard {
    @include cardRadius($cardRadius, $cardRadius, $cardRadius, $cardRadius);
    @include cardShadow($cardShadowColor);
    max-width: 60rem;

    &[data-cardTheme="data-offerCard-default"] {
        margin-top: 0; }
    &--type {
        padding: .2em 1em;
        border-radius: 3em;
        position: absolute;
        left: 50%;
        top: 0;
        transform: translate(-50%, -50%);
        margin-bottom: 1em;
        font-family: $customFont;
        text-transform: uppercase;
        font-weight: 800;
        font-size: .8rem;
        letter-spacing: .1em;
        color: rgba(black, .7); } }

.siteCard {
    margin: 2em auto 0 auto;
    position: relative;
    padding: 1em;
    background: white;
    margin-top: 1rem;
    &--fig {
        img {
            background: var(--b, grey);
            //background: yellow
            border-radius: 5%; } }
    &--title {
        //margin-top: 1em
        //border: 1px solid red
        padding: 0 .5em;
        font-size: 1rem;
        font-family: sans-serif;
        small {
            display: block; } }
    &--offer, &--tac {
        font-family: $customFont;
        line-height: 1.2em;
        padding: .5em 0; }
    &--tac {
        max-width: 60em; }
    &--offer {
        font-size: 1.7rem;
        p {
            margin-bottom: 0; } }
    &--snippets {
        margin: 1em 0; }
    &--cta {
        margin-top: .5em;
        width: 100%;
        text-align: center;
        padding: .5em 0;
        border-top: 1px solid rgba(grey, .15);
        .rating {
            margin-bottom: .5em; }
        .wdwbtn {
            font-weight: normal;
            width: $siteCardBtnWidth;
 }            //margin: 0 .25em
        //a.wdwbtn
 }        //    border: 1px solid red
    &--tac {
        line-height: 1em;
        margin-bottom: 0;
        margin: 0 auto 0 auto;
        text-align: center;
        color: rgba(grey, .7); } }



// 500 mobile details
////////////////////////////////////////////////////////////////////// 500 MOBILE - DETAILS
@media (min-width: $siteCardBPM) {
    .siteCard {
        &--fig {}
        &--title, &--offer {
            padding: 0 .8rem; }
        &--title {
            small {
                display: inline;
                &::before {
                    content: " - "; } } }
        &--offer {}
        //&--cta
        .wdwbtn {
            width: $siteCardBtnWidth;
            margin: 0 .25em;
            font-weight: normal; }
        &--tac {
            text-align: center; } } }


// 900 mobile details
////////////////////////////////////////////////////////////////////// 900 MOBILE - DETAILS
@media (min-width: $siteCardBPL) {
    .siteCard {
        //> *
        //    border: 1px solid red

        &--fig {}
        &--title, &--offer {
            padding: 0 1rem; }
        &--title {}
        small {
            display: inline;
            &::before {
                content: " - "; } }
        &--offer {}
        &--snippets {
            border-top: 1px solid rgba(grey, .15);
            padding-top: 1em; }
        &--cta {
            border: none;
            a {
                margin: 0; }
            .cta--pn {
                //border: 1px solid red
                margin: 0; }
            .cta--rm {
                border: none;
                float: left; } }
        &--tac {
            text-align: center; } } }
