.wysiwyg {
	max-width: 60rem;
	margin: 0 auto;
	img {
		width: 100%;
		margin-bottom: 2em;
		max-width: 60em;
		height: auto; } }
@media (min-width: $wysiwygBPM) {
	.home {
		.wysiwyg {
			display: grid;
			margin: 2em auto 0 auto;
			grid-template-columns: 1fr 1fr;
			grid-gap: 1.5em;
			section.primary {
				grid-column: 1 / 3; } } } }


