@supports(display: grid) {
    // mobile
    ////////////////////////////////////////////////////////////////////// MOBILE - LAYOUT
    .siteCard {
        display: grid;
        grid-template-columns: 1fr;
        //> *
        //    border: 1px solid blue !important
        //grid-template-rows: repeat(auto)
        &--head {
            grid-column: 1 / 2;
            grid-row: 1 / 2; }
        &--snippets {
            grid-column: 1 / 2;
            grid-row: 2 / 3; }
        &--cta {
            grid-column: 1 / 2;
            grid-row: 3 / 4;
            justify-self: center; } }

    .siteCard {
        &--head {
            display: grid;
            grid-template-columns: [img] 80px [head] 1fr;
 }            //grid-template-rows: repeat(auto)
        &--fig {
            grid-column: img;
            grid-row: 1 / span -1; }
        &--title {
            grid-column: head;
            grid-row: 1 / 2;
            align-self: center; }
        &--offer {
            grid-column: img / span 2;
            grid-row: 2 / 3; } }

    // 500 mobile layout
    ////////////////////////////////////////////////////////////////////// 500 MOBILE - LAYOUT
    @media (min-width: $siteCardBPM) {
        .siteCard {
            //border: 1px solid red
            &--head {
                display: grid;
                grid-template-columns: 100px 1fr;
                grid-template-rows: repeat(auto); }
            &--fig {
                grid-column: 1;
                grid-row: 1 / span 2; }
            &--title {
                grid-column: 2;
                grid-row: 1 / 2;
                align-self: center; }
            &--offer {
                grid-column: 2;
                grid-row: 2 / 3; } } }

    // 900 mobile layout
    ////////////////////////////////////////////////////////////////////// 900 MOBILE - LAYOUT
    @media (min-width: $siteCardBPL) {
        .siteCard {
            //border: 1px solid red
            display: grid;
            grid-template-columns: 5em 1fr $siteCardBtnWidth * 2.6;
            //grid-template-rows: repeat(auto)
            &--head {
                grid-column: 1 / 3;
                grid-row: 1 / 2; }
            &--snippets {
                grid-column: 1 / -1;
                grid-row: 2 / 3; }
            &--cta {
                grid-column: 3 / 4;
                grid-row: 1 / 2;
                justify-self: center; } }
        .siteCard {
            &--head {
                display: grid;
                grid-template-columns: [img] 90px [head] 1fr;
                //grid-template-rows: minmax(1em, .5fr) minmax(1em, 1fr)
                grid-template-rows: 2em 1fr; }
            &--fig {
                align-self: center; }
            &--title {
                align-self: end; }
            &--offer {
                align-self: start; } } } }
