$exclusive: saturate(lighten($wdwGreen3, 10%), 0);
$new: saturate(lighten($wdwOrange1, 35%), 100);
//$new: saturate(lighten($wdwBlue4, 35%), 100)
//$design: desaturate(lighten(#ffea00, 10%), 0)
$design: desaturate(lighten($wdwPurple, 35%), 0);
$default: desaturate(lighten($wdwBlue, 10%), 0);
//lighten(#fff885, 0%)

article[data-cardTheme="data-offerCard-exclusive"] {
	--borderColor: rgba($exclusive, .5);
	--bgColor: darken($exclusive, 10%);
	--linkColor: darken($exclusive, 35%);
	//--starsBg: white

	// button
	--btn-bg: darken($exclusive, 35%);
	--btn-bg-hover: darken($exclusive, 30%);
	--btn-color: rgba(white, .9);
	--btn-color-hover: rgba(white, 1); }

article[data-cardTheme="data-offerCard-new"] {
	--borderColor: rgba($design, .5);
	--bgColor: darken($design, 10%);
	--linkColor: darken($design, 20%);
	//--starsBg: white

	// button
	--btn-bg: darken($design, 35%);
	--btn-bg-hover: darken($design, 30%);
	--btn-color: rgba(white, .9);
	--btn-color-hover: rgba(white, 1); }

article[data-cardTheme="data-offerCard-new-design"] {
	--borderColor: rgba($new, .5);
	--bgColor: darken($new, 10%);
	--linkColor: darken($new, 20%);
	//--starsBg: white

	// button
	--btn-bg: darken($new, 35%);
	--btn-bg-hover: darken($new, 30%);
	--btn-color: rgba(white, .9);
	--btn-color-hover: rgba(white, 1); }


//.offerCard
article[data-cardTheme="data-offerCard-default"] {
	//--borderColor: white
	--bgColor: lighten(white, 5%);
	--linkColor: desaturate(darken($default, 20), 50%);
	//--starsBg: $wdwGrey

	// button
	--btn-bg: darken($default, 5%);
	--btn-bg-hover: darken($default, 0%);
	--btn-color: rgba(white, .9);
	--btn-color-hover: rgba(white, 1); }


