.offerCard {
	background: white;
	box-shadow: 0px 15px 20px -15px var(--borderColor, $wdwOrange3);
	//background: var(--bgColor)
	border: 3px solid var(--borderColor);
	//background: linear-gradient(var(--borderColor) 20%, var(--bgColor) 100%)
	h2 {
		& a {
			color: $wdwBlack; } }
	&--Title {
		color: var(--linkColor); }
	.stars--rating {
		.bg {
			fill: var(--starsBg, $wdwGrey); }
		.fg {
			fill: $wdwOrange1; } } }
article {
	.offerCard--type {
		background: var(--bgColor); } }
