.wdwbtn-xl {
	font-size: 1.2rem;
	padding: .7em 0;
	font-family: $customFont; }

// Converted Variables

$blue: $wdwBlue4;
$button-color: lighten($wdwBlue4, 45%);
$button-background-color: $blue;
$border-radius: 5px;
$darken-1: rgba(0, 0, 0, .0625);
$darken-2: rgba(0, 0, 0, .125);
$darken-3: rgba(0, 0, 0, .25);

// Custom Media Query Variables


/* Basscss Btn Primary */

.wdwbtn-primary {
	color: $button-color;
	background-color: $button-background-color;
	border-radius: $border-radius; }

.wdwbtn-primary:hover {
	color: lighten($button-color, 10%);
	background-color: darken($button-background-color, 5%);
	box-shadow: inset 0 0 0 20rem $darken-1; }

.wdwbtn-primary:active {
 }	// box-shadow: inset 0 0 0 20rem $darken-2, inset 0 3px 4px 0 $darken-3, 0 0 1px $darken-2

.wdwbtn-primary:disabled,
.wdwbtn-primary.is-disabled {
	opacity: .5; }

[data-categTheme = "slots"] {
	--btn-bg: $wdwPurple1;
	--btn-bg-hover: darken($wdwPurple1, 5%);
	--btn-color: lighten($wdwPurple1, 45%);
	--btn-color-hover: lighten($wdwPurple1, 60%); }

[data-categTheme = "bingo"] {
	--btn-bg: $wdwBlue1;
	--btn-bg-hover: darken($wdwBlue1, 5%);
	--btn-color: lighten($wdwBlue1, 60%);
	--btn-color-hover: lighten($wdwBlue1, 80%); }


.wdwbtn-themed, .wdwbtn-themed-transparent {
	border-radius: $border-radius; }

.wdwbtn-themed {
	background: var(--btn-bg, $wdwBlue3);
	color: var(--btn-color, lighten($wdwBlue1, 70%));
	box-shadow: 0px 1px 2px var(--btn-bg, $wdwBlue3);
	transition: all 0.3s ease-in-out;
	&:hover {
		background: var(--btn-bg-hover, $wdwBlue4);
		color: var(--btn-color-hover, lighten($wdwBlue1, 80%));
		box-shadow: 0px 7px 8px -5px var(--btn-bg, $wdwBlue3); } }

.wdwbtn-themed-transparent {
	color: var(--btn-bg);
	background: transparent;
	.wdwbtn-themed:hover {
		color: var(--btn-color-hover);
		background: transparent; } }
