@supports(display: grid ) {
	.wdwcontent-left {
		display: grid;
		grid-gap: 1em;
		grid-template-columns: 1fr;
		main {
			padding: 0 1em;
			justify-self: center;
			grid-column: 1 / 2;
			grid-row: 1 / 2;
			//padding: 0 1em
 }			//box-shadow: 5px 0px 10px 0px rgba(black, .05)
		.side {
			padding: 1em;
			padding-top: 1em;
			grid-column: 1 / 2;
			grid-row: 2 / 3;
			//border: 1px solid $wdwGrey1
			//background: $wdwOrange4
			//background: $wdwBlack2
			//background: $wdwGrey
			//background: $wdwBlue1

			//background: linear-gradient(to right, lighten($primaryColor4, 10) 0%, lighten($primaryColor4, 15) 100% )
			> * {
				margin-bottom: 1rem; }
			> ul li {
				margin-bottom: 2.5em; } }
		main, .side {
			padding-top: 1rem; } } }

@media (min-width: 55rem) {
	@supports(display: grid ) {
		.wdwcontent-left {
			display: grid;
			//grid-gap: 1em
			padding: 0 0 0 1em;
			grid-template-columns: 1fr 20em;
			main {
				justify-self: center;
				grid-column: 1 / 2;
				grid-row: 1 / 2; }
			.side {
				padding: 1em;
				grid-column: 2 / 3;
				grid-row: 1 / 2;
				//border: 1px solid $wdwGrey1
 } } } }				//background: white

@media (min-width: 65rem) {
	@supports(display: grid ) {
		.wdwcontent-left {
			grid-template-columns: 1fr 25em; } } }


