$headSectionBorderRadius: 5px;
.headSection {
    display: grid;
    grid-template-columns: 1fr;
    border-radius: $headSectionBorderRadius;
    //padding: 0em 1em
    margin-bottom: 2em;
    > * {
        align-self: center; }
    &--description {
        justify-self: start;
        align-self: stretch; } }

@media (min-width: 40rem) {
    .headSection {
        grid-template-columns: 1fr 1fr;
        > * {
            align-self: center; }
        &--description {
            justify-self: end; } } }
