$tabsRadius: 5px;
$tabsColor: $wdwOrange;
.tabs {
    .nav-tabs {
        border-top-left-radius: $tabsRadius;
        border-top-right-radius: $tabsRadius;
        overflow: hidden;
        background: white;
        //border-bottom: 5px solid darken($wdwOrange, 10%)
        border-bottom: none;
        a {
            text-align: center;
            width: 50%;
            border-top-left-radius: 0;
            border-top-right-radius: 0;
            border: none;
            font-family: $customFont;
            font-size: 1.2rem;
            line-height: 2.5em;
            font-weight: 800;
            color: darken($tabsColor, 25%);
            border-top: $tabsRadius solid lighten($tabsColor, 10%);
            background-color: lighten($tabsColor, 10%);
            margin-bottom: 0px;
            &.active {
                color: $tabsColor;
                border-top: $tabsRadius solid white;
                //border-color: transparent
                &:hover {
                    background: white; } }
            &:hover {
                //background: darken($wdwOrange, 15%)
                //color: white
                border-color: darken($wdwOrange3, 5%); }
            &:focus {
                //color: $wdwBlack
 } } } }                //border-color: darken($wdwOrange, 10%)

