// Converted Variables
$border-width: 1px;
$bold-font-weight: bold;
$button-font-family: inherit;
$button-font-size: inherit;
$button-font-weight: $bold-font-weight;
$button-line-height: 1.125rem;
$button-padding-y: .5rem;
$button-padding-x: 1rem;
$darken-2: rgba(0, 0, 0, .125);
$darken-3: rgba(0, 0, 0, .25);

// Custom Media Query Variables


/* Basscss Btn */

.wdwbtn {
  font-family: $button-font-family;
  font-size: $button-font-size;
  //font-weight: $button-font-weight
  text-decoration: none;
  cursor: pointer;
  display: inline-block;
  line-height: $button-line-height;
  padding: $button-padding-y $button-padding-x;
  margin: 0;
  height: auto;
  border: $border-width solid transparent;
  vertical-align: middle;
  // ???
  //-webkit-appearance: none
  color: inherit;
  background-color: transparent;
  text-shadow: 0px 1px rgba(black, .5); }

.wdwbtn:hover {
  text-decoration: none; }

.wdwbtn:focus {
  outline: none;
  border-color: $darken-2;
  box-shadow: 0 0 0 3px $darken-3; }

::-moz-focus-inner {
  border: 0;
  padding: 0; }
