@supports not (display: grid) {
    // mobile
    ////////////////////////////////////////////////////////////////////// MOBILE - LAYOUT
    .siteCard {
        display: flex;
        flex-direction: column; // row | row-reverse | column

        &--head {
            order: 1; }
        &--snippets {
            order: 3; }
        &--cta {
            order: 2;
            &--rm {
 } } }                // float: right

    .siteCard {
        &--head {
            display: block;
            position: relative;
            flex-direction: row; }
        &--fig {
            float: right;
            position: absolute;
            img {
                min-width: 60px;
                max-width: 60px; } }

        &--title, &--offer {
            display: block;
            margin-left: 60px;
            float: left;
            width: calc(100%-60px);
            padding: 0 .8rem;
 } }            //border: 1px solid red
    // 500 mobile layout
    ////////////////////////////////////////////////////////////////////// 500 MOBILE - LAYOUT
    @media (min-width: $siteCardBPM) {
        .siteCard {
            flex-direction: row;
            flex-direction: column;
            & > * {
                align-self: flex-start; }// auto | flex-start | flex-end | center | baseline | stretch;
            &--head {
                order: 1; }
            &--snippets {
                order: 2; }
            &--cta {
                order: 3; }
            &--cta {
                margin-top: 0; }
            &--title, &--offer {
                min-width: 30rem; } } }



    // 900 mobile layout
    ////////////////////////////////////////////////////////////////////// 900 MOBILE - LAYOUT
    @media (min-width: $siteCardBPL) {
        //[data-desc="true"]
        //    img
        //        border: 1px solid red !important
        .siteCard {
            display: flex;
            //flex-wrap: wrap // nowrap | wrap | wrap-reverse
            //flex-direction: row
            &--head {}
            &--snippets {}
            &--cta {
                margin-right: 0;
                margin-top: 1em;
                border-top: 1px solid rgba(grey, .15);
                //min-width: 20em
                //max-width: 20em
                .cta--rm {
                    float: none; } } }
        .siteCard {
            &--head {}
            &--fig {}
            &--title {}
            &--offer {} } } }
