
.ampTabContainer {
	display: flex;
	flex-wrap: wrap; }

.tabButton[selected] {
	outline: none;
	background: white; }

.tabButton {
	list-style: none;
	flex-grow: 1;
	text-align: center;
	cursor: pointer; }


.tabContent {
	display: none;
	width: 100%;
	order: 1; } // must be greater than the order of the tab buttons to flex to the next line */

.tabButton[selected]+.tabContent {
	display: block;
 }	//outline: 1px solid #fff

// For example below (not required)
.itemCustom {
	border: 1px solid #fff;
	height: 280px;
	width: 380px;
	margin: 10px;
	text-align: center;
	padding-top: 140px; }

amp-selector {
	[option] {
		outline: 1px solid transparent;
		//width: 50%
		text-align: center;
		font-family: $font-family-monospace;

		border-top: 4px solid $primary;
		background: lighten($primary, 57%);
		padding-top: 1rem;
		color: $primary; }

	[option][selected] {
		outline: 1px solid #fff;
		color: $pink;
		border-top: 4px solid $pink; } }



//.nav
.nav-tabs {
	.nav-item {
		width: 50%;
		text-align: center;
		font-family: $font-family-monospace; }
	.nav-link {
		border-radius: 0;
		border: none;
		border-top: 4px solid $primary;
		background: lighten($primary, 57%);
		padding-top: 1rem;
		color: $primary;
		&.active {
			color: $pink;
			border-top: 4px solid $pink; } } }

.tabContent {
	background: white;
	ul {
		margin-bottom: 0; } }
