.headSection {
    max-width: 60em;
    margin-left: auto;
    margin-right: auto;
    background: var(--bgColor, $wdwGreen);
    > * {
        margin-bottom: 0;
        color: white; }
    h2 {
        font-size: 1.5rem;
        text-align: center;
        padding: .5em 0 .5em 0;
        text-transform: uppercase; }
    a {
        color: rgba(white, .9);
        background: rgba($wdwBlack1, .20);
        transition: all 0.3s ease-in-out;
        //padding: .5em .8em
        //line-height: 100%
        &:hover {
            color: rgba(white, 1);
            background: rgba($wdwBlack1, .40);
            text-decoration: none; } }
    &--description {
        border-radius: $headSectionBorderRadius $headSectionBorderRadius 0 0;
        margin: 0 auto;
        display: flex;
        overflow: hidden;
        height: 100%;
        span {
            //background: rgba($wdwBlack1, .20)
            padding: 0 1em;
            align-self: center; } } }

@media (min-width: 40rem) {
    .headSection {
        h2 {
            text-align: left;
            padding: .5em 0 .5em .5em; }
        &--description {
            margin: 0;
            border-radius: 0 $headSectionBorderRadius $headSectionBorderRadius 0; } } }
