.svg-plus18 {
	//border: 1px solid red
	height: 25px;
	width: 145px;
	--svg-stroke: black;
	--svg-fill: none; }

footer {
    --svg-stroke: white;
    --svg-fill: none;
    .plus18 {
        --svg-fill: none;
        --svg-stroke: white;
        stroke-width: 3;
        stroke-miterlimit: 10; } }

.plus18 {
    stroke: var(--svg-stroke);
    fill: var(--svg-fill); }
